@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';
.wrapper.book_cast {

	.contents_wrap {
		padding-top: 0;
	}

	.book_cast_menu {
		position: relative;
		height: 70px;

		.sps {
			height: 70px;
			background: #f7f7f7;

			&.sps-blw {
				position: fixed;
				width: 100%;
				min-width: variables.$defaultMinWidth;
				left: 0;
				right: 0;
				top: 0;

				.book_cast_menu_inner {

					.logo_link, .gap {
						display: inline-block;

					}

					.logo_book_cast {
						width: 87px;
						height: 20px;
						margin: 17px 0 9px;
						background-size: 87px 20px;

						@include mixins.ratio_images('../images/img_logo_book_cast_sm', 'png', 87px, 20px);
					}
				}
			}
		}

		.book_cast_menu_inner {
			@include mixins.justify_width(center, between);
			width: 1200px;
			height: 70px;
			min-height: 44px;
			margin: 0 auto;

			.logo_wrap, .search_wrap {
				line-height: 1;
			}

			.logo_wrap {
				.logo_link {
					display: none;
					width: 118px;
					height: 46px;
					background: {
						position: 0 0;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/img_logo_kyobo_sm', 'png', 118px, 46px);
				}

				.logo_book_cast {
					display: inline-block;
					width: 110px;
					height: 26px;
					margin-top: 2px;
					background: {
						position: 0 0;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/img_logo_book_cast', 'png', 110px, 26px);
				}

				.gap {
					display: none;
					width: 1px;
					height: 16px;
					margin: 16px 17px 0 20px;
					background: #d5d5d5;
					font-size: 0;
					vertical-align: top;
				}
			}

			.search_wrap {
				position: relative;

				// TODO : 북캐스트 검색 좌측 메뉴 버튼 기획 / 디자인 변경으로 빠지는것으로 알고 있음 확인 후 삭제 필요.
				.menu_list {
					display: inline-block;
					margin-right: 34px;
					@include mixins.clearfix;

					li {
						float: left;

						a {
							display: inline-block;
							color: variables.$textColorGray;
							@include mixins.font_size_xs;
							line-height: 32px;
							font-weight: variables.$fontWeightMedium;

							&.active {
								color: #fff;
								padding: 0 16px;
								border-radius: 16px;
								background: #292929;
								font-weight: variables.$fontWeightBold;
							}
						}

						+ li {
							margin-left: 20px;
						}
					}

					+ .btn_search_open {
						margin-top: 4px;
					}
				}

				.btn_search_open {
					width: 24px;
					height: 24px;
					background: {
						position: center;
						repeat: no-repeat;
					}
					@include mixins.ratio_images('../images/btn_book_cast_search', 'png', 18px, 18px);
					font-size: 0;
				}

				.form_search_box {
					position: absolute;
					display: none;
					width: auto;
					right: -14px;
					top: -6px;

					.form_ip {
						width: 320px;
						border-radius: 22px;
					}
				}

				.search_util_box {
					position: absolute;
					display: none;
					width: 440px;
					background: #fff;
					border: 1px solid #000;
					border-radius: 16px;
					padding: 20px 30px 30px;
					box-sizing: border-box;
					right: -14px;
					top: 54px;

					.recent_keyword_box {
						width: 100%;
					}

					.search_util_bottom {
						margin-top: 20px;
						padding-top: 21px;
						border-top: 1px solid #eaeaea;
					}
				}

				&.active {
					.form_search_box {
						display: inline-block;
					}

					.search_util_box {
						display: inline-block;
					}
				}
			}
		}

		&.transparent {
			position: absolute;
			width: 100%;
			min-width: variables.$defaultMinWidth;
			left: 0;
			right: 0;
			top: 0;

			.sps {
				background: transparent;

				&.sps-blw {
					background: #f7f7f7;
				}
			}

			~ .book_cast_wrap {
				padding-top: 0;
			}
		}

		&.white_type {

			.sps {

				&.sps-blw {

					.book_cast_menu_inner {
						.search_wrap {
							.menu_list {
								li {
									a:not(.active) {
										color: variables.$textColorGray;
									}
								}
							}
						}
					}
				}

				&:not(.sps-blw) {
					.book_cast_menu_inner {
						.logo_book_cast {
							@include mixins.ratio_images('../images/img_logo_book_cast_white', 'png', 110px, 26px);
						}

						.search_wrap {
							.btn_search_open {
								@include mixins.ratio_images('../images/btn_book_cast_search_white', 'png', 18px, 18px);
							}
						}
					}
				}
			}

			.book_cast_menu_inner {
				.search_wrap {
					.menu_list {
						li {
							a {
								color: #fff;
							}
						}
					}
				}
			}
		}
	}

	.book_cast_wrap {
		padding-top: 60px;

		.title_size_xxl {
			.btn_wish {
				margin-left: 14px;
			}
		}
	}

	.bookcast_main_content_area {
		margin-top: 110px;

		.tag_wrap {
			margin-bottom: 40px;
		}

		&.vogue {
			background-color: variables.$colorGray06;
			padding: 120px 0;

			.swiper-button-prev, .swiper-button-next {
				margin-top: 0;
				top: 114px;
				background-color: transparent;
			}
		}

		&.history {
			.swiper-button-prev, .swiper-button-next {
				margin-top: 0;
				top: 114px;
			}
		}

		&.subscribe {
			position: relative;
			padding-top: 120px;

			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 400px;
				background-color: variables.$colorGray06;
			}

			.my_subscribe_detail {
				position: relative;

				> .prod_area {
					width: 100%;
					align-items: flex-start;

					.prod_thumb_box {
						width: 436px;
						height: 436px;

						.img_box {
							img {
								max-height: none;
							}
						}
					}

					.prod_info_box {
						margin-left: 36px;
					}
				}

				.subscribe_more_area {
					position: absolute;
					left: 472px;
					right: 0;
					bottom: 0;

					.prod_list {
						.prod_area {
							&.type_bookcast {
								.prod_info_box {
									margin-top: 14px;

									.prod_name {
										font-weight: variables.$fontWeightRegular;
										@include mixins.font_size_xs;
									}
								}
							}
						}
					}

					&.no_data {
						@include mixins.justify_width(center, center);
						border: 1px solid variables.$colorGray04;
						border-radius: 10px;
						width: 728px;
						height: 204px;
						padding: 0;

						.no_data_desc {
							&:before {
								width: 48px;
								height: 48px;
								margin-bottom: 16px;
								@include mixins.ratio_images('../images/ico_bookcast_main_nodata', 'png', 48px, 48px);
							}
						}
					}
				}

			}
		}

		&.original {
			padding: 117px 0 121px;
			@include mixins.ratio_images('../images/bg_kyobo_original', 'png', 'cover');

			.bookcast_prod_swiper_wrap {
				.swiper-button-prev, .swiper-button-next {
					top: 106px;
					margin-top: 0;
					background-color: transparent;
				}
			}
		}

		&.k_class {
			.k_class_prefix {
				@include mixins.font_size_md;

				&:before {
					content: '';
					display: inline-block;
					width: 1px;
					height: 14px;
					margin: 8px 20px 0;
					background-color: variables.$colorGray02;
					vertical-align: top;
				}
			}

			.prod_list {
				.prod_item {
					width: calc((100% - 36px) / 4);
					margin-right: 12px;

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}

		&.login_induce {
			height: 170px;
			background-color: #ddefd8;

			.contents_inner {
				position: relative;
				@include mixins.justify_width(center);
				height: 100%;

				.login_induce_desc {
					position: relative;
					font-size: 22px;
					font-weight: variables.$fontWeightBold;
					color: #8da088;
				}

				.btn_wrap {
					margin-left: auto;

					.btn_login_induce {
						display: inline-block;
						width: 110px;
						height: 40px;
						padding: 9px 5px 0;
						border: 1px solid #54b13f;
						background-color: #54b13f;
						@include mixins.font_size_sm;
						font-weight: variables.$fontWeightMedium;
						border-radius: 3px;
						vertical-align: top;
						box-sizing: border-box;
						text-align: center;
						color: #fff;

						~ .btn_login_induce {
							margin-left: 6px;
						}

						&.type_line {
							background-color: transparent;
							border-color: #6e8b66;
							color: #6e8b66;
						}
					}
				}

				&:before {
					content: '';
					position: absolute;
					top: 0;
					left: 493px;
					display: block;
					width: 396px;
					height: 216px;
					@include mixins.ratio_images('../images/img_bookcast_login_before', 'png', 396px, 216px);
				}
			}
		}
	}

	.prod_list_wrap {
		&.k_class_list {
			width: 1132px;
		}

		&.event_list {
			padding-right: 1px;
		}

		.pagination {
			padding-top: 0;
			margin-top: 60px;
			border-top: none;
		}
	}
}

.join_induce_box {
	@include mixins.justify_width(center);
	height: 170px;
	@include mixins.ratio_images('../images/bg_join_induce', 'png', 1200px, 170px);

	.join_induce_link {
		display: block;
		height: 100%;
		width: 100%;
		padding: 0 60px;
		@include mixins.justify_width(center);
	}

	.join_induce_desc {
		@include mixins.font_size_md;
		color: #fff;
	}

	.btn_more_view {
		margin-left: auto;
		flex-shrink: 0;

		.ico_arw {
			display: inline-block;
			vertical-align: top;
		}
	}
}

// 북캐스트 상품카드 : blur background image 타입박스
.live_timeline_box {
	overflow: hidden;
	position: relative;
	padding: 43px 48px;
	border-radius: 20px;

	.blur_img_box {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		bottom: 0;

		img {
			width: 100%;
			height: 100%;
		}

		&:after {
			display: block;
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: {
				color: #fff;
			}
			opacity: .9;
		}
	}

	.prod_area.type_bookcast {
		position: relative;
		border-radius: 0;
		box-shadow: none;
		z-index: 1;

		.prod_flag_group {
			display: block;
			margin: 0 0 12px;

			.prod_onair,
			.btn_alarm {
				margin-right: 4px;

				&:last-child {
					margin-right: 0;
				}
			}
		}

		.prod_onair {
			@include mixins.justify_width(start, start, start, true);
			align-items: center;
			padding: 5px 12px 3px;
			@include mixins.font_size_xs();
			border-radius: 15px;
			background-color: #767675;
			vertical-align: middle;

			span {
				display: inline-block;
				color: #fff;
				vertical-align: top;
			}

			&:before {
				display: block;
				content: "";
				width: 13px;
				height: 13px;
				margin: 0 2px 0 0;
				background: {
					repeat: no-repeat;
					position: center;
				}
				@include mixins.ratio_images('../images/ico_onair_clock', 'png', 13px, 13px);
			}

			&.today {
				background-color: variables.$kyoboGreen;
			}
		}

		&.horizontal {
			.prod_thumb_box {
				width: 165px;
			}

			.prod_info_box {
				margin-left: 34px;
			}
		}
	}
}

// Editor View Wrap
.edit_view_wrap {
	.edit_header_box {
		position: relative;
		padding: 0 180px 0 0;
		@include mixins.font_size_xs();

		.flag_cate {
			display: inline-block;
			font-weight: variables.$fontWeightBold;
			color: #767675;
		}

		.edit_title_case {
			margin: 5px 0 0;

			.name {
				@include mixins.font_size_lg();
				font-weight: variables.$fontWeightBold;
				// @include mixins.ellipsis();
			}

			&:first-child {
				margin: 0;
			}
		}

		.edit_util_case {
			position: absolute;
			top: 24px;
			right: 0;

			& > a, .sns_share_wrap {
				margin: 0 6px 0 0;

				&:last-child {
					margin: 0;
				}
			}
		}
	}

	.edit_container_box {
		margin: 36px 0 0;

		.edit_content_box {
			margin: 20px 0 0;
			padding: 30px 20px 70px;
			border-top: 1px solid variables.$colorGray04;
			border-bottom: 1px solid variables.$colorGray04;
		}

		.event_content_box {
			margin: 20px 0 0;

			img {
				max-width: 100%;
			}
		}
	}

	.edit_bookcast_box {
		position: relative;

		padding: 18px 28px;
		border-radius: 10px;
		background-color: variables.$colorGray06;

		.bookcast_name {
			display: inline-block;
			@include mixins.font_size_def();
		}

		.bookcast_name, .cast_partner, .cast_popular {
			margin: 0 6px 0 0;

			&:last-child {
				margin: 0;
			}
		}

		.btn_subscribe {
			position: absolute;
			right: 28px;
			top: 50%;
			@include mixins.transform(translate(0, -50%));
		}

	}

}

// 이벤트 리뷰 / 댓글
.event_review_wrap {
	margin: 70px 0 0;

	.title_wrap {
		.title_heading {
			margin-right: 6px;
		}

		.right_area {
			.btn_sm {
				margin-left: 20px;
			}
		}
	}
}

.board_wrap {
	.no_data {
		border-top: 1px solid variables.$textColorBlack;
		border-bottom: 1px solid variables.$colorGray04;
	}
}

// 서브 메인에 해당하는 화면 헤더영역
.book_cast_header {
	margin-bottom: 60px;
	padding-top: 108px;
	min-height: 372px;

	&:before {
		content: '';
		position: absolute;
		display: inline-block;
		width: calc(100% / 2 - 55px);
		height: 400px;
		margin-left: auto;
		background: #f2f2f2;
		right: 0;
		top: 0;
	}

	.visual {
		position: relative;
		margin-right: calc((100% - #{variables.$defaultInnerWidth}) / 2);
		border-top-right-radius: 20px;
		overflow: hidden;
		text-align: right;
		z-index: 1;

		.text_box {
			position: absolute;
			color: #fff;
			width: variables.$defaultInnerWidth;
			text-align: left;
			right: 0;
			top: 75px;

			.visual_title {
				@include mixins.font_size_xxl;
			}

			.visual_desc {
				margin-top: 16px;
				@include mixins.font_size_sm;
			}

			.btn_subscribe, .series_end {
				margin-top: 31px;
			}

			// 연재완료된 시리즈 입니다.
			.series_end {
				display: inline-block;
				color: rgba(255, 255, 255, .7);
				border: 1px solid rgba(255, 255, 255, .5);
				border-radius: 22px;
				font-size: variables.$fontSizeXs;
				line-height: 44px;
				padding: 0 20px;

				.ico_subscribe {
					display: inline-block;
					width: 14px;
					height: 14px;
					margin: {
						bottom: 2px;
						right: 4px;
					}
					background: {
						position: center;
						repeat: no-repeat;
					}
					font-size: 0;
					vertical-align: middle;
					@include mixins.ratio_images('../images/ico_subscribe_check_w', 'png', 14px, 14px);
					opacity: .7;
				}
			}

		}

		// 북캐스터 상단 프로필
		.caster_profile_wrap {
			position: absolute;
			width: variables.$defaultInnerWidth;
			min-height: 90px;
			box-sizing: border-box;
			padding-left: 162px;
			padding-right: 72px;
			text-align: left;
			right: 0;
			top: 70px;

			.caster_img {
				position: absolute;
				left: 0;
				top: 0;
				display: block;
				width: 90px;
				height: 90px;
				overflow: hidden;
				border-radius: 50%;
				background-color: variables.$colorGray02;

				img {
					display: block;
					width: 100%;
					min-height: 100%;
				}
			}

			.caster_name_area {
				@include mixins.justify_width(center, start, row);

				.caster_name {
					@include mixins.font_size_xxl;
					@include mixins.ellipsis(false, true, true);
					max-width: calc(100% - 120px);
					font-weight: variables.$fontWeightBold;
					color: #fff;

					& + [class*='btn_'] {
						margin-left: 6px;
					}
				}
			}

			.caster_simple_info {
				@include mixins.clearfix;
				margin-top: 12px;

				dl.caster_info_cnt {
					float: left;

					dt {
						display: inline-block;
						@include mixins.font_size_sm;
						font-weight: variables.$fontWeightMedium;
						color: #fff;
					}

					dd {
						display: inline-block;
						@include mixins.font_size_sm;
						font-weight: variables.$fontWeightBold;
						color: #fff;
					}
				}

				.gap {
					float: left;
					display: block;
					width: 1px;
					height: 10px;
					overflow: hidden;
					margin: 5px 10px 0;
					background-color: rgba(255, 255, 255, 0.5);
					font-size: 0;
					text-indent: -9999px;
				}
			}

			.caster_introduce {
				width: 100%;
				max-height: 58px;
				margin-top: 20px;
				@include mixins.font_size_sm;
				color: #fff;
			}

			.btn_subscribe {
				margin-top: 30px;
			}
		}
	}
}

/* 북캐스트 메인 라이브 배너 swiper 영역 */
.bookcast_live_banner_wrap {
	overflow: hidden;
	position: relative;

	.bookcast_live_thumb {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		padding: 115px 0 83px;
		z-index: 2;

		.swiper-slide {
			width: 1323px;

			.live_banner_box {
				position: relative;
				width: 1026px;
				margin: 0 auto;
				overflow: hidden;
				border-radius: 20px;

				&:before {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					display: block;
					width: 100%;
					height: 100%;
					background-color: variables.$textColorBlack;
					opacity: .4;
					z-index: 2;
				}

				.bookcast_live_link {
					display: block;
					height: 100%;
				}

				.flag {
					opacity: 0;
					position: absolute;
					top: 0;
					left: 0;
					display: inline-block;
					height: 36px;
					padding: 5px 16px 0;
					border-radius: 20px 0;
					background-color: #ff393b;
					font-size: 18px;
					font-weight: variables.$fontWeightBold;
					letter-spacing: variables.$letterSpacing;
					vertical-align: top;
					color: #fff;
					box-sizing: border-box;
					z-index: 2;

					&:before {
						content: '';
						display: inline-block;
						width: 5px;
						height: 5px;
						margin: 10px 6px 0 0;
						border-radius: 100%;
						background-color: #fff;
						vertical-align: top;
					}
				}

				.bookcast_video_box {
					position: relative;
					width: 100%;

					img {
						width: 100%;
					}

					video {
						width: 100%;
					}
				}

				.prod_area {
					opacity: 0;
					position: absolute;
					left: 40px;
					bottom: 31px;
					max-width: calc(100% - 230px);

					.prod_thumb_box {
						width: 60px;
						height: 60px;

						.img_box {
							border-radius: 4px;

							&:before {
								content: none;
							}
						}
					}

					.prod_info_box {
						margin-left: 12px;

						.prod_name {
							@include mixins.font_size_xs;
							color: #fff;
						}

						.prod_price {
							margin-top: 0;

							.price {
								.val, .unit {
									@include mixins.font_size_xxl;
									color: #fff;
									line-height: 1;
								}

								.unit {
									font-size: 28px;
								}
							}

							.percent {
								@include mixins.font_size_xxl;
								color: #ff393b;
								line-height: 1;
							}
						}
					}
				}

				.btn_lg {
					opacity: 0;
					position: absolute;
					right: 40px;
					bottom: 42px;
					min-width: 130px;
					width: 130px;
				}
			}

			&.swiper-slide-active {
				.live_banner_box {
					&:before {
						content: none;
					}

					.bookcast_video_box {
						&:before {
							content: '';
							position: absolute;
							left: 0;
							bottom: 0;
							width: 100%;
							height: 231px;
							background: linear-gradient(180deg, rgba(variables.$textColorBlack, 0), rgba(variables.$textColorBlack, 1) 231px);
							opacity: .8;
						}
					}

					.flag, .prod_area, .btn_lg {
						opacity: 1;
						@include mixins.transition(opacity .23s);
					}
				}
			}
		}

		.swiper_control_floating_box {
			position: absolute;
			left: 50%;
			top: 50%;
			@include mixins.transform(translate(-50%, -50%));
			z-index: 2;

			.swiper-button-prev {
				left: -589px;
			}

			.swiper-button-next {
				right: -589px;
			}

			.swiper-button-prev, .swiper-button-next {
				position: absolute;
				width: 54px;
				height: 54px;
				border: 1px solid variables.$colorGray06;
				border-radius: 100%;
				background-color: transparent;
				z-index: 2;
			}
		}

		.swiper_control_box {
			width: 1026px;
			padding: 0 10px;
			margin: 18px auto 0;
			text-align: left;
			box-sizing: border-box;

			.pagination_group {
				position: relative;
				display: inline-block;
				padding: 0 24px;
				vertical-align: top;
				line-height: 19px;

				.swiper-pagination {
					&.fraction {
						padding: 0;
						transform: none;
						font-size: 0;
					}
				}

				.swiper-pagination-current, .swiper-pagination-total {
					position: absolute;
					top: 0;
					left: 0;
					@include mixins.font_size_xs;
					line-height: 19px;
					vertical-align: top;
					color: #fff;
				}

				.swiper-pagination-total {
					left: auto;
					right: 0;

					&:before {
						content: none;
					}
				}

				.swiper-pagination-progressbar {
					width: 70px;
					height: 1px;
					margin-top: 9px;
					background-color: rgba(255, 255, 255, .15);

					.swiper-pagination-progressbar-fill {
						width: 100%;
						height: 100%;
						background-color: #fff;
					}
				}
			}

			.swiper_current {
				font-weight: variables.$fontWeightBold;
			}

			.btn_auto_control {
				display: inline-block;
				width: 19px;
				height: 19px;
				background-color: #f39;
				vertical-align: top;
			}
		}
	}

	.bookcast_live_blur {
		height: 813px;

		.blur_img_box {
			position: relative;
			overflow: hidden;
			height: 100%;

			img {
				position: absolute;
				top: 50%;
				left: 0;
				@include mixins.transform(translateY(-50%));
				display: block;
				width: 100%;
			}

			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: rgba(variables.$textColorBlack, .8);
				z-index: 2;
			}
		}
	}

	&.type_vertical {
		.bookcast_live_thumb {
			left: 50%;
			width: 1503px;
			@include mixins.transform(translateX(-50%));

			.swiper-wrapper {
				align-items: center;
			}

			.swiper-slide {
				width: 501px;

				&:not(.swiper-slide-active) {
					transform: scale(0.572) !important;
				}

				.live_banner_box {
					width: calc(100% - 68px);
					border-radius: 16px;

					.flag {
						border-radius: 16px 0 20px 0;
					}

					.prod_area {
						left: 20px;
						bottom: 20px;
						max-width: calc(100% - 155px);

						.prod_thumb_box {
							width: 54px;
							height: 54px;
						}

						.prod_info_box {
							margin-left: 10px;

							.prod_name {
								font-size: variables.$fontSizeXxs;
							}

							.prod_price {
								.percent {
									font-size: variables.$fontSizeLg;
								}

								.price {
									.val {
										font-size: variables.$fontSizeLg;
									}

									.unit {
										font-size: 22px;
									}
								}
							}
						}
					}

					.btn_lg {
						right: 20px;
						bottom: 25px;
						min-width: 100px;
						width: 100px;
						height: 44px;
						@include mixins.font_size_xs;
						font-weight: variables.$fontWeightMedium;
					}
				}

				&.swiper-slide-active {
					//width: 501px;

					.live_banner_box {
						width: calc(100% - 67px);
					}
				}

				&.swiper-slide-prev {
					.live_banner_box {
						margin: 0 0 0 auto;
					}
				}

				&.swiper-slide-next {
					.live_banner_box {
						margin: 0 auto 0 0;
					}
				}
			}

			.swiper_control_floating_box {
				.swiper-button-prev {
					left: -304px;
				}

				.swiper-button-next {
					right: -304px;
				}

			}

			.swiper_control_box {
				text-align: center;
			}
		}
	}
}

/* 북캐스트 이벤트 */
.bookcast_event_banner_wrap {
